:root {
  --primary-color: #3498db;
  --primary-color-light: #ebf5fb;
  --secondary-color: #2ecc71;
  --text-color: #34495e;
  --border-color: #e0e0e0;
  --highlight-color: rgba(52, 152, 219, 0.2);
  --hover-color: rgba(52, 152, 219, 0.1);
  --weekend-color: #e74c3c;
  --box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
}

body {
  font-family: 'Arial', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
}

button {
  border: none;
  border-radius: var(--border-radius);
  padding: 0.6rem 1.2rem;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.1s;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.header {
  text-align: center;
  margin-bottom: 2rem;
}

.header h1 {
  font-size: 2.5rem;
  color: var(--primary-color);
}

.container {
  display: flex;
  gap: 2rem;
}

.schedule-section, .call-roster-section, .summary-section {
  width: 100%;
  background-color: var(--card-background);
  border-radius: 8px;
  box-shadow: var(--box-shadow);
  padding: 1.5rem;
  box-sizing: border-box;
}

.calendar-container {
  font-size: 0.9rem;
}

.react-calendar {
  width: 100%;
  max-width: 100%;
  background: white;
  border: 1px solid var(--border-color);
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.125em;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: var(--box-shadow);
  margin-top: 1rem;
}

.react-calendar__month-view__days {
  display: grid !important;
  grid-template-columns: repeat(7, 1fr);
  gap: 2px; /* Add small white space between days */
}

.react-calendar__tile {
  max-width: 100%;
  padding: 0.75rem 0.5rem;
  background: none;
  text-align: center;
  line-height: 16px;
  font: inherit;
  font-size: 1em;
  border-radius: 4px;
}

.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  background-color: var(--hover-color);
}

.react-calendar__tile--now {
  background: var(--primary-color-light);
  font-weight: bold;
}

.react-calendar__tile--active {
  background: var(--primary-color);
  color: white;
}

.react-calendar__tile--rangeStart,
.react-calendar__tile--rangeEnd {
  background-color: var(--primary-color);
  color: white;
}

.react-calendar__tile--rangeBetween {
  background-color: var(--highlight-color);
  color: var(--text-color); /* Ensure dark text for dates between first and last */
}

/* Additional styles for better readability */
.react-calendar__tile p {
  margin: 0;
  font-size: 0.8em;
  margin-top: 0.25rem;
  color: var(--text-color); /* Ensure dark text for scheduled names */
}

.resident-info {
  background-color: #f1f8ff;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  margin-bottom: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button {
  border: none;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-right: 0.5rem;
}

button:last-child {
  margin-right: 0;
}

.remove-button {
  background-color: #e74c3c;
  color: white;
}

.generate-button {
  background-color: var(--secondary-color);
  color: white;
}

.clear-button {
  background-color: #e67e22;
  color: white;
}

@media (max-width: 768px) {
  .App {
    padding: 0.5rem;
  }

  .container {
    flex-direction: column;
  }

  .schedule-section, .call-roster-section {
    width: 100%;
    padding: 1rem;
  }

  .header h1 {
    font-size: 1.8rem;
  }

  button {
    width: 100%;
    margin-right: 0;
    margin-bottom: 0.5rem;
  }

  .shift-summary table, .shift-summary th, .shift-summary td {
    display: block;
    width: 100%;
  }

  .shift-summary th, .shift-summary td {
    box-sizing: border-box;
  }

  .remove-button {
    width: 30%;
  }

  /* New styles for mobile calendar */
  .react-calendar {
    padding: 0.5rem;
  }

  .react-calendar__month-view__days {
    gap: 1px;
  }

  .react-calendar__tile {
    padding: 0.5rem 0.25rem;
    font-size: 0.8em;
  }

  .react-calendar__tile p {
    font-size: 0.7em;
  }

  .react-calendar__navigation button {
    padding: 0.25rem;
  }
}

/* Additional style for very small screens */
@media (max-width: 360px) {
  .react-calendar__tile {
    padding: 0.4rem 0.2rem;
    font-size: 0.7em;
  }

  .react-calendar__tile p {
    font-size: 0.6em;
  }
}

.copyright {
  margin-top: 20px;
  font-size: 0.8rem;
  color: #666;
}

label {
  margin-right: 10px;
}

input {
  width: 96.5%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  margin-bottom: 10px;
  margin-right: auto;
}

.form-container {
  margin: 1rem;
}

.call-freq-in {
  margin-left: 10px;
  width: 25px;
  transform: translateY(3px);
}